<template>
    <!-- Modal -->
<div class="modal fade" id="exampleModal"
 tabindex="-1" aria-labelledby="exampleModalLabel"
  aria-hidden="true" ref="modal">
  <div class="modal-dialog modal-xl" role="document">
  <div class="modal-content border-0">
    <div class="modal-header bg-dark text-white">
      <h5 class="modal-title" id="exampleModalLabel">
        <span>新增產品</span>
      </h5>
      <button type="button" class="btn-close"
              data-bs-dismiss="modal" aria-label="Close"></button>
    </div>
    <div class="modal-body">
      <div class="row">
        <div class="col-sm-4">
          <div class="mb-3">
            <label for="image" class="form-label">輸入圖片網址</label>
            <input type="text" class="form-control" id="image"
                    placeholder="請輸入圖片連結">
          </div>
          <div class="mb-3">
            <label for="customFile" class="form-label">或 上傳圖片

            </label>
            <input type="file" id="customFile" class="form-control"
            ref="fileInput"  @change="uploadFile">
          </div>
          <img class="img-fluid" :src="tempProduct.imageUrl">

          <div class="mb-3">
            <label for="customFile" class="form-label mt-3">圖片二

            </label>
            <input type="file" id="customFile2" class="form-control"
             ref="files2" @change="Imgfn2">
          </div>
          <img class="img-fluid" :src="tempProduct.imageUrl2">

          <div class="mb-3">
            <label for="customFile" class="form-label mt-3">圖片三

            </label>
            <input type="file" id="customFile2" class="form-control"
             ref="files3" @change="Imgfn3">
          </div>
          <img class="img-fluid" :src="tempProduct.imageUrl3">
          <!-- <div class="mt-5">
            <div class="mb-3 input-group" >
              <input type="url" class="form-control form-control"
                      placeholder="請輸入連結">
              <button type="button" class="btn btn-outline-danger">
                移除
              </button>
            </div>
            <div>
              <button class="btn btn-outline-primary btn-sm d-block w-100">
                新增圖片
              </button>
            </div>
          </div> -->
        </div>
        <div class="col-sm-8">
          <div class="mb-3">
            <label for="title" class="form-label">標題</label>
            <input type="text" class="form-control" id="title"
                    placeholder="請輸入標題" v-model="tempProduct.title">
          </div>

          <div class="row gx-2">
            <div class="mb-3 col-md-6">
              <label for="category" class="form-label">分類</label>
              <input type="text" class="form-control" id="category"
                      placeholder="請輸入分類" v-model="tempProduct.category">
            </div>
            <div class="mb-3 col-md-6">
              <label for="price" class="form-label">單位</label>
              <input type="text" class="form-control" id="unit"
                      placeholder="請輸入單位" v-model="tempProduct.unit">
            </div>
          </div>

          <div class="row gx-2">
            <div class="mb-3 col-md-6">
              <label for="origin_price" class="form-label">原價</label>
              <input type="number" class="form-control" id="origin_price"
                      placeholder="請輸入原價" v-model="tempProduct.origin_price">
            </div>
            <div class="mb-3 col-md-6">
              <label for="price" class="form-label">售價</label>
              <input type="number" class="form-control" id="price"
                      placeholder="請輸入售價" v-model="tempProduct.price">
            </div>
          </div>
          <hr>

          <div class="mb-3">
            <label for="description" class="form-label">產品描述</label>
            <textarea type="text" class="form-control" id="description"
                      placeholder="請輸入產品描述" v-model="tempProduct.description"></textarea>
          </div>
          <div class="mb-3">
            <label for="content" class="form-label">說明內容</label>
            <textarea type="text" class="form-control" id="content"
                      placeholder="請輸入產品說明內容" v-model="tempProduct.content"></textarea>
          </div>
          <div class="mb-3">
            <div class="form-check">
              <input class="form-check-input" type="checkbox"
                      :true-value="1"
                      :false-value="0"
                      v-model="tempProduct.is_enabled"
                      id="is_enabled">
              <label class="form-check-label" for="is_enabled">
                是否啟用
              </label>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="modal-footer">
      <button type="button" class="btn btn-outline-secondary"
              data-bs-dismiss="modal">取消
      </button>
      <button type="button" class="btn btn-primary"
      @click="clickUpdate">確認</button>
    </div>
  </div>
</div>
</div>
</template>

<script>
import Modal from 'bootstrap/js/dist/modal'

export default {
  props: {
    product: {
      type: Object,
      default () { return {} }
    }
  },
  watch: {
    product () {
      this.tempProduct = this.product
    }
  },
  data () {
    return {
      modal: {},
      tempProduct: {},
      isLoading: false
    }
  },
  methods: {
    showModal () {
      this.modal.show()
    },
    hideModal () {
      this.modal.hide()
    },
    clickUpdate () {
      this.$emit('update-product', this.tempProduct)
    },
    uploadFile () {
      const uploadeFile = this.$refs.fileInput.files[0]
      // 這是JS的格式
      const formData = new FormData()
      // 第一個參數是文件中的name名稱
      formData.append('file-to-upload', uploadeFile)
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/upload`
      this.axios.post(url, formData)
        .then(res => {
          console.log(res.data)
          if (res.data.success) {
            this.tempProduct.imageUrl = res.data.imageUrl
          }
        })
    },
    Imgfn2 () {
      const Img2 = this.$refs.files2.files[0]
      const formData = new FormData()
      formData.append('file-to-upload', Img2)
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/upload`
      this.axios.post(url, formData)
        .then(res => {
          if (res.data.success) {
            this.tempProduct.imageUrl2 = res.data.imageUrl
          }
        })
    },
    Imgfn3 () {
      const Img3 = this.$refs.files3.files[0]
      const formData = new FormData()
      formData.append('file-to-upload', Img3)
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/upload`
      this.axios.post(url, formData)
        .then(res => {
          if (res.data.success) {
            this.tempProduct.imageUrl3 = res.data.imageUrl
          }
        })
    }
  },
  mounted () {
    this.modal = new Modal(this.$refs.modal)
    console.log(this.modal)
  }
}
</script>
